


























import {
  Component, Emit, Prop, Vue,
} from 'vue-property-decorator';
import { IActivityItem } from '@/types/interface';

@Component
export default class BaseScrollList extends Vue {
  @Prop() list!: IActivityItem[];

  @Emit()
  select(id: string) {
    return id;
  }

  flag = 0;

  handleScroll(e: any) {
    const left = e.target.scrollLeft;
    this.flag = Math.floor(left / 250);
  }
}
